// ** React Imports
import { useState, createContext, useContext } from "react";

// ** Intl Provider Import
import { IntlProvider } from "react-intl";

// ** Core Language Data
import messagesEn from "../../assets/data/locales/en.json";
import messagesDe from "../../assets/data/locales/de.json";
import messagesFr from "../../assets/data/locales/fr.json";
import messagesAr from "../../assets/data/locales/ar.json";

// ** User Language Data
import userMessagesEn from "../../assets/data/locales/en.json";
import userMessagesDe from "../../assets/data/locales/de.json";
import userMessagesFr from "../../assets/data/locales/fr.json";
import userMessagesAr from "../../assets/data/locales/ar.json";
import { useConfigs } from "../hooks/useConfigs";
import { useTranslation } from "@hooks/useTranslation";

// ** Menu msg obj
const menuMessages = {
  en: { ...messagesEn, ...userMessagesEn },
  de: { ...messagesDe, ...userMessagesDe },
  fr: { ...messagesFr, ...userMessagesFr },
  ar: { ...messagesAr, ...userMessagesAr },
};

// ** Create Context
const Context = createContext();

const IntlProviderWrapper = ({ children }) => {
  const configs = useConfigs();
  const default_lang =
    localStorage.getItem("lang")?? configs?.default_language;
  // ** States
  const [locale, setLocale] = useState(default_lang);
  const [messages, setMessages] = useState(menuMessages[default_lang]);

  const { setLang } = useTranslation(default_lang);

  // ** Switches Language
  const switchLanguage = (lang) => {
    setLocale(lang);
    setMessages(menuMessages[lang]);
    setLang(lang);
  };
  return (
    <Context.Provider value={{ locale, switchLanguage }}>
      <IntlProvider
        key={locale}
        locale={locale}
        messages={messages}
        defaultLocale={default_lang}
      >
        {children}
      </IntlProvider>
    </Context.Provider>
  );
};

export { IntlProviderWrapper, Context as IntlContext };
