import { useState } from "react";
import { ar, en } from "../../data/locales";

const dict = { ar, en };

/**
 *
 */
export const useTranslation = (language) => {
  let storage = localStorage.getItem("lang");
  storage = storage == "undefined" ? undefined : storage;
  const [lang, set_lang] = useState(language ?? storage ?? "ar");

  const setLang = (lang) => {
    const l = dict[lang];
    if (l) {
      localStorage.setItem("lang", lang);
      set_lang((l) => lang);
    }
    return l;
  };

  const t = (message) => {
    return dict[lang][message] ?? message;
  };

  return { t, setLang };
};
