export default ({ children }) => {
    const configs = useConfigs() 
    const [baseURL,setURL] =useState( configs.REACT_APP_API_HOST);
     
    useEffect(() => {
         
         
        
        if (baseURL) 
            apiClient.reCreate({ baseURL: baseURL })
    }, [baseURL])


    return <>
        {children}
    </>
}