import { ADM_SCREEN_GRANT_ACTION_TYPES } from "./action-types";

const initialState = {
  isLoading: false,
  data: [],
  item: {},
  errorMsg: '',
  isFulfilled: false, // Indicates whether the request has been fulfilled or not
}

const systemScreens = {
  '/Landing': true,
  '/misc/not-authorized': true,
  '/misc/error': true,
}

export default function AdmRoutingScreenGrantReducer(state = initialState, action) {
  switch (action.type) {
    case ADM_SCREEN_GRANT_ACTION_TYPES.GET_SCREEN_GRANT_BY_USER_ROLE_CODE_REQUEST:
      return { ...state, isLoading: true, isFulfilled: false }

    case ADM_SCREEN_GRANT_ACTION_TYPES.GET_SCREEN_GRANT_BY_USER_ROLE_CODE_REQUEST_SUCCESS:
      const screens = {}
      action.payload.forEach(item => { if(item.screenUrl) screens[item.screenUrl] = true })
      return { ...state, isLoading: false, isFulfilled: true, data: { ...screens, ...systemScreens } }

    case ADM_SCREEN_GRANT_ACTION_TYPES.GET_SCREEN_GRANT_BY_USER_ROLE_CODE_REQUEST_ERROR:
      return { ...state, isLoading: false, isFulfilled: false, errorMsg: action.payload }

    default: return state
  }
}
