import { ADM_SCREEN_GRANT_ACTION_TYPES } from "./action-types";

const initialState = {
  isLoading: false,
  data: [],
  item: {},
  errorMsg: ''
}

export default function AdmScreenGrantReducer(state = initialState, action) {
  switch (action.type) {
    case ADM_SCREEN_GRANT_ACTION_TYPES.LIST_ADM_SCREEN_GRANT_REQUEST:
      return { ...state, isLoading: true }

    case ADM_SCREEN_GRANT_ACTION_TYPES.LIST_ADM_SCREEN_GRANT_REQUEST_SUCCESS:
      return { ...state, isLoading: false, data: action.payload }

    case ADM_SCREEN_GRANT_ACTION_TYPES.LIST_ADM_SCREEN_GRANT_REQUEST_ERROR:
      return { ...state, isLoading: false, errorMsg: action.payload }

    case ADM_SCREEN_GRANT_ACTION_TYPES.CREATE_ADM_SCREEN_GRANT_REQUEST:
      return { ...state, isLoading: true }

    case ADM_SCREEN_GRANT_ACTION_TYPES.CREATE_ADM_SCREEN_GRANT_REQUEST_SUCCESS:
      return { ...state, isLoading: false, item: action.payload }

    case ADM_SCREEN_GRANT_ACTION_TYPES.CREATE_ADM_SCREEN_GRANT_REQUEST_ERROR:
      return { ...state, isLoading: false, errorMsg: action.payload }

    case ADM_SCREEN_GRANT_ACTION_TYPES.UPDATE_ADM_SCREEN_GRANT_REQUEST:
      return { ...state, isLoading: true }

    case ADM_SCREEN_GRANT_ACTION_TYPES.UPDATE_ADM_SCREEN_GRANT_REQUEST_SUCCESS:
      return { ...state, isLoading: false, item: action.payload }

    case ADM_SCREEN_GRANT_ACTION_TYPES.UPDATE_ADM_SCREEN_GRANT_REQUEST_ERROR:
      return { ...state, isLoading: false, errorMsg: action.payload }

    case ADM_SCREEN_GRANT_ACTION_TYPES.GET_ADM_SCREEN_GRANT_REQUEST:
      return { ...state, isLoading: true }

    case ADM_SCREEN_GRANT_ACTION_TYPES.GET_ADM_SCREEN_GRANT_REQUEST_SUCCESS:
      return { ...state, isLoading: false, item: action.payload }

    case ADM_SCREEN_GRANT_ACTION_TYPES.GET_ADM_SCREEN_GRANT_REQUEST_ERROR:
      return { ...state, isLoading: false, errorMsg: action.payload }

    case ADM_SCREEN_GRANT_ACTION_TYPES.DELETE_ADM_SCREEN_GRANT_REQUEST:
      return { ...state, isLoading: true }

    case ADM_SCREEN_GRANT_ACTION_TYPES.DELETE_ADM_SCREEN_GRANT_REQUEST_SUCCESS:
      return { ...state, isLoading: false, item: action.payload }

    case ADM_SCREEN_GRANT_ACTION_TYPES.DELETE_ADM_SCREEN_GRANT_REQUEST_ERROR:
      return { ...state, isLoading: false, errorMsg: action.payload }

    case ADM_SCREEN_GRANT_ACTION_TYPES.LIST_SCREEN_GRANT_BY_USER_ROLE_CODE_REQUEST:
      return state

    case ADM_SCREEN_GRANT_ACTION_TYPES.LIST_SCREEN_GRANT_BY_USER_ROLE_CODE_REQUEST_SUCCESS:
      return { ...state, data: action.payload }

    case ADM_SCREEN_GRANT_ACTION_TYPES.LIST_SCREEN_GRANT_BY_USER_ROLE_CODE_REQUEST_ERROR:
      return { ...state, errorMsg: action.payload }

    case ADM_SCREEN_GRANT_ACTION_TYPES.LIST_SCREEN_GRANT_FOR_USER_ROLE_CODE_REQUEST:
      return { ...state, isLoading: true }

    case ADM_SCREEN_GRANT_ACTION_TYPES.LIST_SCREEN_GRANT_FOR_USER_ROLE_CODE_REQUEST_SUCCESS:
      return { ...state, isLoading: false, data: action.payload }

    case ADM_SCREEN_GRANT_ACTION_TYPES.LIST_SCREEN_GRANT_FOR_USER_ROLE_CODE_REQUEST_FAILURE:
      return { ...state, isLoading: false, errorMsg: action.payload }

    default: return state
  }
}
