// ** Redux Imports
import { combineReducers } from "redux";
// ** Reducers Imports
import auth from "./auth";
import navbar from "./navbar";
import layout from "./layout";
import navbarReducer from "./navbar" 
import usersReducer from "../auther/users/reducer" 
import AdmScreenGrantReducer from "../screenGrant/reducer"
import AdmRoutingScreenGrantReducer from "../screenGrant/RoutingReducer";

const rootReducer = combineReducers({
    auth,
    navbar,
    layout,
    navbarReducer,
    usersReducer, 
    AdmScreenGrantReducer,
    AdmRoutingScreenGrantReducer,
});

export default rootReducer;